import React, { useEffect } from "react";
import { Col, Container, Row, Image } from "react-bootstrap";
import { useDispatch } from "react-redux";
import { getAllComments } from "../../action/commentAction";
import Footer from "../Footer";
import AllBlog from "./blogscreen/AllBlog";

const BlogScreen = () => {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getAllComments());
  }, [dispatch]);
  const BannerURL =
    "https://tse2.mm.bing.net/th?id=OIP.a5YOm_1N-oe-O025Jw4PTQHaE8&pid=Api&P=0&w=246&h=165";
  return (
    <>
      <Container>
        <Image src={BannerURL} alt="BannerImage" width="100%" height="300px" />
        <br />
        <br />
        <Row>
          <Col>
            <AllBlog />
          </Col>
        </Row>
      </Container>
      <br />
      <Footer />
    </>
  );
};

export default BlogScreen;
